<template>
  <div class="main">
    <div>
      
      <!-- 二级路由插座 -->
      <router-view></router-view>

    </div>
    <!-- 底部导航 -->
    <van-tabbar v-model="tabBarName"  @change="toggleTabBar"  active-color="#690000">
      <van-tabbar-item v-for="(item, index) in tabBarData" :key="index" :icon="item.icon">{{item.title}}</van-tabbar-item>
    </van-tabbar>
  </div>
</template>

<script>
  export default {
    data() {
      return {
        tabBarName: 0,
        
        //底部导航数据
        tabBarData: [
          {
            icon: 'shop-o',
            title: '元気',
            routeName: 'Home',
            url: '/main/home'
          },
          {
            icon: 'shop-o',
            title: '分类',
            routeName: 'Category',
            url: '/main/category'
          },
          // {
          //   icon: 'records',
          //   title: '订单',
          //   routeName: 'Order',
          //   url: '/main/order'
          // },
          {
            icon: 'shopping-cart-o',
            title: '购物车',
            routeName: 'Shopcart',
            url: '/main/shopcart'
          },
          {
            icon: 'user-o',
            title: '我的',
            routeName: 'My',
            url: '/main/my'
          }
        ]
      };
    },
     created() {
      //获取路径的hash
      let hash = location.hash.slice(1);

      //根据路径激活相应路由
      for (let i = 0; i < this.tabBarData.length; i++) {
        if (this.tabBarData[i].url == hash) {
          this.tabBarName = i;
          break;
        }
      }
    },

    methods: {
      //切换底部导航
      toggleTabBar(index) {
        // console.log('index', index);
        //切换根据下标跳转相应的routeName页面路由
        this.$router.push({name: this.tabBarData[index].routeName});
      }
    }
  }
</script>

<style lang="less" scoped>

</style>